.main{
&__aboutMe {
  width: 100%;
  &__cover{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
    width: 100vw;
    height: 100vh;
    background-color:#f7484e;
  }
  &__content {
    color: #0c0c0c;
    padding: 25px;
    &--formation {
      padding: 15px 0;
      color:  #0c0c0c;
      &--box {
        box-shadow: 5px 5px 5px #2f434470;
        border-bottom: 1px solid #33323242;
        border-left: 1px solid #33323242;
        .bootcamp-view {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          margin-top: 10px;
          &:hover  > .formation__bootcamp--arrow {
            color: orange;
          }
        }
        .master__header-container {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          margin-top: 10px;
          &:hover  > .button-form {
            color: orange;
          }
        }
        .master__form {
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          font-size: 13px;
          
        }
        .validateInputsSection {
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          font-size: 13px;
        }
        .view-hidden {
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          font-size: 13px;
        }
        .formation-container {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          margin-top: 10px;
          &:hover  > .button-form {
            color: orange;
          }
        }
        .courses-view {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          border-bottom: 1px solid #33323242;
          border-left: 1px solid #33323242;
          padding: 3px 5px;
          margin-top: 10px;
          &:hover  > .formation__courses--arrow {
            color: orange;
          }
        }
      }
    }
    .paragraph-linkedin {
      padding: 24px 0 10px;
      cursor: pointer;
    }
    .download-cv-button {
      padding: 5px;
      text-align: center;
      width: 180px;
      margin: auto;
      border: 3px solid #33323242;
      padding: 8px;
      box-shadow: 5px 5px 5px #2f434470;
      a {
        color:  #0c0c0c;
      }
    }
  }
  @include tablet {
    display: flex;
    flex-direction: row;
    &__cover{
      width: 50vw;
      height: 100vh;
    }
    &__content{
      width: 50vw;
      .skillsContainer{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .main__aboutMe__content--formation--box {
          margin-top: 20px;
        }
        .skillsWraper{
          width: 248px;
        }
        .seo{
          margin-top: 15px;
        }
      }
    }
  }
}
}

@import url(https://fonts.googleapis.com/css?family=Open+Sans:800);

.text--transparent {
  fill: transparent;
}

.anim-shape {
  -webkit-transform-origin: 0x 150px;
  -ms-transform-origin: 0x 150px;
  transform-origin: 0x 150px;
  -webkit-transform: scale(0, 1) translate(0, 0);
  -ms-transform: scale(0, 1) translate(0, 0);
  transform: scale(0, 1) translate(0, 0);
  -webkit-animation: moving-panel 5s ease-in-out alternate;
  animation: moving-panel 5s ease-in-out alternate;
}

.colortext .anim-shape:nth-child(1) {
  fill: #0E1826;
}

.colortext .anim-shape:nth-child(2) {
  //fill: #166973;
  fill: #F26444;
}

.colortext .anim-shape:nth-child(3) {
  //fill: #65BFA6;
  fill: #F26444;
}

.colortext .anim-shape:nth-child(4) {
  fill: #F2CD5C;
}

.colortext .anim-shape:nth-child(5) {
  fill: #F26444;
}

.shadow {
  -webkit-transform: translate(10px, 10px);
  -ms-transform: translate(10px, 10px);
  transform: translate(10px, 10px);
}

.anim-shape--shadow {
  fill: #000;
  fill-opacity: .2;
}

@-webkit-keyframes moving-panel {
  100% {
    -webkit-transform: scale(1, 1) translate(20px, 0);
    transform: scale(1, 1) translate(20px, 0);
  }
}

@keyframes moving-panel {
  100% {
    -webkit-transform: scale(1, 1) translate(20px, 0);
    transform: scale(1, 1) translate(20px, 0);
  }
}

